import React, { Component } from 'react';
import './App.css';

import { ImageFilters } from './config';
import Camera from './components/Camera';
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imagePreview: null,
      selectedFilter: null,
      isCameraScreen: false
    };
  }

  componentDidMount() {
    this.elem = document.documentElement;
  }

  openFullscreen = () => {
    if (this.elem.requestFullscreen) {
      this.elem.requestFullscreen();
    } else if (this.elem.mozRequestFullScreen) { /* Firefox */
      this.elem.mozRequestFullScreen();
    } else if (this.elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
      this.elem.webkitRequestFullscreen();
    } else if (this.elem.msRequestFullscreen) { /* IE/Edge */
      this.elem.msRequestFullscreen();
    }
  }

  /* Close fullscreen */
  closeFullscreen = () => {
    if (document.exitFullscreen) {
      document.exitFullscreen();
    } else if (document.mozCancelFullScreen) { /* Firefox */
      document.mozCancelFullScreen();
    } else if (document.webkitExitFullscreen) { /* Chrome, Safari and Opera */
      document.webkitExitFullscreen();
    } else if (document.msExitFullscreen) { /* IE/Edge */
      document.msExitFullscreen();
    }
  }

  submitForm = () => {

  }

  onSelectChange = (e) => {
    let filter = ImageFilters.find(el => el.value === e.target.value);
    this.setState({ imagePreview: filter.srcImg, selectedFilter: filter.srcImg });
  }

  takePhoto = () => {
    if(this.state.selectedFilter){
      this.openFullscreen();
      this.setState({ isCameraScreen : true });
    } else {
      alert("Please select filter!");
    }
  }

  goToMainScreen = () => {
    this.closeFullscreen();
    this.setState({ isCameraScreen : false });
  }

  render() {
    const { imagePreview, isCameraScreen, selectedFilter } = this.state;
    return (
      <div className="App">
        {!isCameraScreen &&
          (<div className="main-screen">
            <header>
              <h1>Crazy Selfies</h1>
            </header>
            <section>
              <form>
                <input type="text" name="Name" placeholder="Enter your name" />

                <select name="filters" id="selectedFilter" onChange={this.onSelectChange}>
                  {ImageFilters.map(filter => (<option key={"option-" + filter.value} value={filter.value}>{filter.name}</option>))}
                </select>

                <input type="button" value="Take Photo" name="takephoto" onClick={this.takePhoto} />

                {imagePreview && <img src={imagePreview} alt="Selfie Preview" className="preview" />}

                <input type="submit" value="Submit" onClick={this.submitForm} />

              </form>
            </section>
          </div>)}
        {isCameraScreen &&
          <Camera
            filterImg={selectedFilter}
            closeCamera={this.goToMainScreen}
          />
        }
      </div>
    );
  }
}

export default App;
