import React, { Component } from 'react';
import './Camera.css';
import CameraPhoto, { FACING_MODES, IMAGE_TYPES } from 'jslib-html5-camera-photo';

class Camera extends Component {
    constructor(props, context) {
        super(props, context);
        this.cameraPhoto = null;
        this.videoRef = React.createRef();
        this.state = {
            dataUri: ''
        }
    }

    componentDidMount() {
        this.cameraPhoto = new CameraPhoto(this.videoRef.current);

        this.startCamera(FACING_MODES.USER, {
            width: (window.innerWidth > 0) ? window.innerWidth : window.screen.width, 
            height: (window.innerHeight > 0) ? window.innerHeight - 50 : window.screen.height - 50});
    }

    startCamera(idealFacingMode, idealResolution) {
        this.cameraPhoto.startCamera(idealFacingMode, idealResolution)
            .then(() => {
                console.log('camera is started !');
            })
            .catch((error) => {
                console.error('Camera not started!', error);
            });
    }

    startCameraMaxResolution(idealFacingMode) {
        this.cameraPhoto.startCameraMaxResolution(idealFacingMode)
            .then(() => {
                console.log('camera is started !');
            })
            .catch((error) => {
                console.error('Camera not started!', error);
            });
    }

    takePhoto() {
        const config = {
            sizeFactor: 1
        };

        let dataUri = this.cameraPhoto.getDataUri(config);
        this.setState({ dataUri });
    }

    stopCamera() {
        this.cameraPhoto.stopCamera()
            .then(() => {
                console.log('Camera stoped!');
            })
            .catch((error) => {
                console.log('No camera to stop!:', error);
            });
    }

    render() {
        const { closeCamera, filterImg } = this.props;
        return (
            <div className="camera-container">
                <video
                    ref={this.videoRef} autoPlay={true}></video>
                <img src={filterImg} alt="Filter Overlay"/>
                <button id="goBack" onClick={() => {
                    this.stopCamera();
                    closeCamera();
                }}>Go back</button>
            </div>
        );
    }
}

export default Camera;